                                            <div v-if="Machine.next_pm_date.early_type=='prdfig'">
                                                <VueGauge :refid="'lopg'+Machine.id"
                                                    :options="{ 'needleValue': Machine.next_pm_date.production_fig, 'arcDelimiters': get_del(Machine.production_fig_threshold,Number(Machine.next_pm_date.settings.prdfig_red), Number(Machine.next_pm_date.settings.prdfig_orange), Number(Machine.next_pm_date.settings.prdfig_yellow), Number(Machine.next_pm_date.settings.prdfig_green)), arcColors: ['green', 'yellow', 'orange','red'], chartWidth: '80' ,rangeLabel: ['0', Machine.production_fig_threshold.toString()],arcLabels:[Machine.next_pm_date.settings.prdfig_yellow.toString(),
                                                    Machine.next_pm_date.settings.prdfig_orange.toString(), Machine.next_pm_date.settings.prdfig_red.toString()],arcOverEffect:true}" />


                                            </div>
                                        </td>
                                        <td class="align-middle">{{ Machine.purchase_date|myDate }}</td>
                                        <td class="align-middle">{{ Machine.condition_of_machine }}</td>
                                        <td class="align-middle">{{ Machine.current_site? Machine.current_site.site_name
                                        : '-' }}</td>
                                        <!-- <td><img v-bind:src="'/' + Machine.photo" width="100" alt="Machine"></td> -->
                                        <td class="align-middle">
                                            <router-link :to="'/machines/edit/' + Machine.id"
                                                class="btn btn-success btn-sm">
                                                Edit </router-link>
                                            <a href="javascript:void(0);"
                                                @click="AssingMachine(Machine.id,Machine.current_site)"
                                                title="Assign to site" class="btn btn-primary btn-sm">
                                                CAM
                                            </a>
                                            <a href="javascript:void(0);" @click="get_machine_list(Machine )"
                                                title="List of Assign to site" class="btn btn-primary btn-sm"
                                                style="background-color: #861cac;">
                                                CAM List
                                            </a>
                                            <!-- <a href="javascript:void(0);" @click="deleteMachine(Machine.id)">
                                                <i class="fa fa-trash red"></i>
                                            </a> -->
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <!--   <pagination :data="Machines" @pagination-change-page="getResults" v-if="Machines">
                            </pagination> -->
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>


        </div>

        <!-- Modal -->
        <div class="modal fade" id="pmModal" tabindex="-1" role="dialog" aria-labelledby="pmModal" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">View PM Details</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <div class="modal-body">


                        <div class="container">
                            <p>
                                <strong>Far No :</strong> {{pmModalData.far_no}}
                                <br>
                                <strong>Machine Name :</strong> {{pmModalData.name}}
                            </p>

                            <div class="row bcon">
                                <div class="col-6 pb-3" v-if="pmModalData.is_maintenance_date">
                                    <VueGauge refid="popg1" :key="'popg'+pmModalData.id"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.maintenance_date, 'arcDelimiters': [Number(pmModalData.next_pm_date.settings.date_red), Number(pmModalData.next_pm_date.settings.date_orange), Number(pmModalData.next_pm_date.settings.date_yellow)], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '180' ,rangeLabel: ['0', pmModalData.date_threshold.toString()],arcLabels:[pmModalData.next_pm_date.settings.date_red.toString(), pmModalData.next_pm_date.settings.date_orange.toString(), pmModalData.next_pm_date.settings.date_yellow.toString(), pmModalData.next_pm_date.settings.date_green.toString()],arcOverEffect:true}" />
                                    <p class="text-center">Remaining Days</p>
                                </div>
                                <div class="col-6 pb-3" v-if="pmModalData.is_consumtion_km_run">
                                    <VueGauge refid="popg2" :key="'popg'+pmModalData.id" :options="{ 'needleValue': pmModalData.next_pm_date.km_run, 'arcDelimiters': get_del(pmModalData.run_km_threshold,Number(pmModalData.next_pm_date.settings.km_red),Number(pmModalData.next_pm_date.settings.km_orange),Number(pmModalData.next_pm_date.settings.km_yellow),Number(pmModalData.next_pm_date.settings.km_green)),
                                    arcColors: [ 'green', 'yellow', 'orange','red'],
                                    chartWidth: '180',
                                    rangeLabel: ['0', pmModalData.run_km_threshold.toString()],arcLabels:[
                                    pmModalData.next_pm_date.settings.km_yellow.toString(),
                                    pmModalData.next_pm_date.settings.km_orange.toString(),
                                    pmModalData.next_pm_date.settings.km_red.toString()
                                    ],arcOverEffect:true}" />
                                    <p class="text-center">Remaining KM</p>
                                </div>
                                <div class="col-6" v-if="pmModalData.is_consumtion_hr_run">
                                    <VueGauge refid="popg3" :key="'popg'+pmModalData.id" :options="{ 'needleValue': pmModalData.next_pm_date.hr_run, 'arcDelimiters': get_del(pmModalData.run_hour_threshold,Number(pmModalData.next_pm_date.settings.hr_red), Number(pmModalData.next_pm_date.settings.hr_orange), Number(pmModalData.next_pm_date.settings.hr_yellow), Number(pmModalData.next_pm_date.settings.hr_green)), arcColors: ['green', 'yellow', 'orange','red'], chartWidth: '180' ,rangeLabel: ['0', pmModalData.run_hour_threshold.toString()],arcLabels:[
                                            pmModalData.next_pm_date.settings.hr_yellow.toString(),
                                            pmModalData.next_pm_date.settings.hr_orange.toString(),
                                            pmModalData.next_pm_date.settings.hr_red.toString(),
                                    ],arcOverEffect:true}" />

                                    <p class="text-center">Remaining Hours</p>
                                </div>
                                <div class="col-6" v-if="pmModalData.is_production_fig">
                                    <VueGauge refid="popg4" :key="'popg'+pmModalData.id"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.production_fig, 'arcDelimiters': get_del(pmModalData.production_fig_threshold,Number(pmModalData.next_pm_date.settings.prdfig_red), Number(pmModalData.next_pm_date.settings.prdfig_orange), Number(pmModalData.next_pm_date.settings.prdfig_yellow), Number(pmModalData.next_pm_date.settings.prdfig_green)), arcColors: ['green', 'yellow', 'orange','red'], chartWidth: '180' ,rangeLabel: ['0', pmModalData.production_fig_threshold.toString()],arcLabels:[pmModalData.next_pm_date.settings.prdfig_yellow.toString(),
                                        pmModalData.next_pm_date.settings.prdfig_orange.toString(), pmModalData.next_pm_date.settings.prdfig_red.toString()],arcOverEffect:true}" />
                                    <p class="text-center">Remaining Production</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>


        <div class="modal fade" id="assignModal" tabindex="-1" role="dialog" aria-labelledby="assignModal"
            aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Machine Asssign to Site</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <form @submit.prevent="submitAssignSite()">
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Existing Site</label>
                                        <input type="text" class="form-control" readonly
                                            :value="machine_existing_site.site_name">
                                    </div>
                                </div>

                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Allocate to Site</label>
                                        <v-select label="site_name" :reduce="(option) => option.id" :options="sites"
                                            :selectable="(option) => isSourceSelectable(option)"
                                            placeholder="Choose Site ..." v-model="form.site_id" v-validate="'required'"
                                            :class="{ 'is-invalid': verrors.has('site_id') }" data-vv-as="site"
                                            name="site_id" class="required">
                                        </v-select>
                                        <div v-if="verrors.has('site_id')" class="help-block invalid-feedback">
                                            {{ verrors.first('site_id') }}
                                        </div>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>From Date</label>
                                        <datetime value-zone="Asia/Kolkata" v-model="form.from_date" name="from_date"
                                            input-class="form-control required"
                                            :class="{ 'is-invalid': verrors.has('from_date') }" v-validate="'required'"  type="datetime"
                                            data-vv-as="From Date" :min-datetime="machine_existing_site.from_date? new Date(machine_existing_site.from_date).toISOString():''" />
                                        <div v-if="verrors.has('from_date')" class="help-block invalid-feedback"> {{
                                        verrors.first('from_date')
                                        }}
                                        </div>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="form-group">
                                        <label>Reason for Redeployment</label>
                                        <textarea v-model="form.redeployment_reason" name="redeployment_reason"
                                            class="form-control required"
                                            :class="{ 'is-invalid': verrors.has('redeployment_reason') }"
                                            v-validate="'required'" data-vv-as="redeployment reason" />
                                        <div v-if="verrors.has('redeployment_reason')"
                                            class="help-block invalid-feedback"> {{
                                            verrors.first('redeployment_reason')
                                            }}
                                        </div>
                                    </div>
                                </div>



                            </div>
                        </div>

                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                            <button type="submit" class="btn btn-primary">Create CAM</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>




        <div class="modal" id="CamListModal">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">

                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">CAM List</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <p>
                                <strong>Far No :</strong> {{pmModalData.far_no}}
                                <br>
                                <strong>Machine Name :</strong> {{pmModalData.name}}
                            </p>
                        <table class="table table-striped">
                            <thead>
                                <tr>
                                    <th width="20">Site Name</th>
                                    <th width="20">From Date</th>
                                    <th width="60">Redeployment Reason</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,x) in cam_list" :key="x" v-if="cam_list">
                                    <td v-html="item.site_name"></td>
                                    <td>{{item.from_date|myDate}}</td>
                                    <td>{{item.redeployment_reason}}</td>
                                </tr>

                            </tbody>
                        </table>
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
                    </div>

                </div>
            </div>
        </div>

        <div class="modal" id="MachineSiteModal">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">

                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">Machine History</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <p>
                                <strong>Far No :</strong> {{pmModalData.far_no}}
                                <br>
                                <strong>Machine Name :</strong> {{pmModalData.name}}
                            </p>
                        <table class="table table-striped">
                            <thead>
                                <tr>
                                    <th>Site Name</th>
                                    <th>From Date</th>
                                    <th>Total Run Hour</th>
                                    <th>Total KM Run</th>
                                    <th>Total Production</th>
                                    <th>Fuel Consumption</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item,x) in MachineSite" :key="x" v-if="MachineSite">
                                    <td v-html="item.site_name"></td>
                                    <td>{{item.from_date|myDate}}</td>
                                    <td>{{item.total_run_hour}}</td>
                                    <td>{{item.total_km_run}}</td>
                                    <td>{{item.total_production_fig}}</td>
                                    <td>{{item.total_consumption}}</td>
                                </tr>
                            </tbody>
                            <tfoot style="border-top: 2px solid black;">
                                <tr>
                                    <th>Total</th>
                                    <th></th>
                                    <th>{{get_row_total('total_run_hour')}}</th>
                                    <th>{{get_row_total('total_km_run')}}</th>
                                    <th>{{get_row_total('total_production_fig')}}</th>
                                    <th>{{get_row_total('total_consumption')}}</th>
                                </tr>
                            </tfoot>
                        </table>
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
                    </div>

                </div>
            </div>
        </div>

    </section>
</template>

<script>

import VueGauge from 'vue-gauge';
export default {
    name: "Machine",
    components: {
        VueGauge,
    },
    data() {
        return {
            MachineSite: '',
            categories: [],
            sub_categories: [],
            companies: [],
            models: [],
            search: {
                page: 1,
                far_no: '',
                category_id: '',
                sub_category_id: '',
                machine_companies_id: '',
                machine_models_id: '',
                machine_type: '',
                condition_of_machine: '',
                purchase_date_from: '',
                purchase_date_to: '',
                Ownership: '',
                pm_due_status: '',
                site_id: '',
            },
            cam_list: '',
            Machines: {},
            pmModalData: '',
            sites: [],
            machine_existing_site: {
                id:'',
                site_name: '',
                from_date: '',
            },
            form: new Form({
                machine_id: '',
                site_id: '',
                from_date: '',
                to_date: '',
                redeployment_reason: '',

            }),
        }
    },
    beforeCreate() {
        axios.get("api/get_pre_machine")
            .then((res) => {
                this.sites = res.data.data.sites;
                this.categories = res.data.data.Category;
                this.companies = res.data.data.MachineCompany;
            })
    },
    methods: {
         get_row_total(key){
            if(this.MachineSite){




            return this.MachineSite.reduce((accumulator, current) => accumulator + Number(current[key]), 0)
            }else{
                return 0
            }

        },
        isSourceSelectable(op){
           if(op.id!=this.machine_existing_site.id){
            return true;
           }
        },
        reset_filter() {
            this.search = {
                page: 1,
                far_no: '',
                category_id: '',
                sub_category_id: '',
                machine_companies_id: '',
                machine_models_id: '',
                machine_type: '',
                condition_of_machine: '',
                purchase_date_from: '',
                purchase_date_to: '',
                Ownership: '',
                pm_due_status: '',
                site_id: '',
            }
            this.getResults()
        },
        get_del(threshold, red, orange, yellow, green) {
            let l = [100 - green, 100 - orange, 100 - red];
            return [yellow, orange, red];
        },
        async view_pm_details(data) {
            let cloaderd = this.$loading.show({
                container: this.$refs.ref_load_user
            });
            this.pmModalData = data
            $('#pmModal').modal('show');
            cloaderd.hide()
        },
        view_site_details(data) {
            this.pmModalData = data
            $('#MachineSiteModal').modal('show');
            this.MachineSite =''
            let cloaderd = this.$loading.show({
                container: this.$refs.ref_load_user
            });
            axios.get('api/machine/get_site_details/' + data.id)
                .then(res => {
                    this.MachineSite = res.data.data
                    cloaderd.hide()
                })
                .catch(err => {
                    cloaderd.hide()
                })
        },

        getResults(page = 1) {
            this.search.list = 1
            this.search.page = page
            axios.get('api/machine', {
                params: this.search
            }).then(({ data }) => (this.Machines = data.data));


        },
        loadMachines() {
            // if(this.$gate.isAdmin()){
            this.search.list = 1
            axios.get("api/machine", {
                params: this.search
            }).then(({ data }) => (this.Machines = data.data));
            // }
        },

        AssingMachine(id, site) {
            this.form.machine_id = id
            this.machine_existing_site.site_name = site ? site.site_name : ''
            this.machine_existing_site.from_date = site ? site.from_date : ''
            this.machine_existing_site.id = site ? site.id : ''
            $('#assignModal').modal('show');
        },
        get_machine_list(data) {
            this.pmModalData = data
            let cloaderd = this.$loading.show({
                container: this.$refs.ref_load_user
            });
            axios.get('/api/machine/site/' + data.id)
                .then(res => {
                    this.cam_list = res.data.data
                    $('#CamListModal').modal('show');
                    cloaderd.hide()
                })
                .catch(err => {
                    cloaderd.hide()
                })
        },

        async submitAssignSite() {
            this.$validator.validateAll().then(async (valid) => {
                if (valid) {
                    this.verrors.clear();
                    let cloaderd = this.$loading.show({
                        container: this.$refs.ref_load_user
                    });

                    this.form.post('api/machine/assign_site')
                        .then((data) => {
                            this.$validator.reset();
                            cloaderd.hide()
                            this.form.id = data.data.data.id;
                            this.form.reset();
                            this.machine_existing_site = {
                                site_name: '',
                                from_date: '',
                            }
                            $('#assignModal').modal('hide');
                            this.getResults();
                            Toast.fire({
                                icon: 'success',
                                title: data.data.message
                            });
                        })
                        .catch(err => {
                            if (err.response && err.response.data) {
                                this.$setErrorsFromResponse(err.response.data);
                            }
                            cloaderd.hide();
                        })

                }
            });
        },

        deleteMachine(id) {
            Swal.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) => {
                // Send request to the server
                if (result.value) {
                    this.form.delete('api/machine/' + id).then(() => {
                        Swal.fire(
                            'Deleted!',
                            'Your file has been deleted.',
                            'success'
                        );
                        // Fire.$emit('AfterCreate');
                        this.loadMachines();
                    }).catch((data) => {
                        Swal.fire("Failed!", data.message, "warning");
                    });
                }
            })
        },

    },
    created() {
        this.loadMachines();
    },
    watch: {

        async 'search.category_id'(n, o) {
            if (o)
                this.form.sub_category_id = ''
            if (n) {
                axios.get("api/category/" + n)
                    .then((res) => {
                        this.sub_categories = res.data.data;
                    })
            }
        },
        async 'search.machine_companies_id'(n, o) {
            if (o)
                this.form.machine_models_id = ''
            if (n) {
                axios.get("api/model/" + n)
                    .then((res) => {
                        this.models = res.data.data;
                    })
            }
        },
    }

}
</script>
